/* The animation code */
@-webkit-keyframes upAndDownLite {
  0% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); }
  40% {
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px); }
  60% {
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px); }
  100% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); } }
@keyframes upAndDownLite {
  0% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); }
  40% {
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px); }
  60% {
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px); }
  100% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); } }

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes scaleYHighAndLow {
  0% {
    -webkit-transform: scaleY(0.95);
            transform: scaleY(0.95); }
  50% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1); }
  100% {
    -webkit-transform: scaleY(0.95);
            transform: scaleY(0.95); } }

@keyframes scaleYHighAndLow {
  0% {
    -webkit-transform: scaleY(0.95);
            transform: scaleY(0.95); }
  50% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1); }
  100% {
    -webkit-transform: scaleY(0.95);
            transform: scaleY(0.95); } }

@-webkit-keyframes rotateLeftAndRight {
  0% {
    -webkit-transform: rotateZ(-7deg);
            transform: rotateZ(-7deg); }
  50% {
    -webkit-transform: rotateZ(7deg);
            transform: rotateZ(7deg); }
  100% {
    -webkit-transform: rotateZ(-7deg);
            transform: rotateZ(-7deg); } }

@keyframes rotateLeftAndRight {
  0% {
    -webkit-transform: rotateZ(-7deg);
            transform: rotateZ(-7deg); }
  50% {
    -webkit-transform: rotateZ(7deg);
            transform: rotateZ(7deg); }
  100% {
    -webkit-transform: rotateZ(-7deg);
            transform: rotateZ(-7deg); } }

@-webkit-keyframes flash {
  0% {
    background: #ffffff; }
  50% {
    background: #ff2c6b; }
  100% {
    background: #ffffff; } }

@keyframes flash {
  0% {
    background: #ffffff; }
  50% {
    background: #ff2c6b; }
  100% {
    background: #ffffff; } }

@-webkit-keyframes leftRight {
  0% {
    -webkit-transform: translateX(0) scaleX(1);
            transform: translateX(0) scaleX(1);
    background-position: 0% 50%; }
  25% {
    -webkit-transform: translateX(200%) scaleX(0.2);
            transform: translateX(200%) scaleX(0.2); }
  50% {
    -webkit-transform: translateX(410%) scaleX(1);
            transform: translateX(410%) scaleX(1);
    background-position: 100% 50%; }
  75% {
    -webkit-transform: translateX(200%) scaleX(0.2);
            transform: translateX(200%) scaleX(0.2); }
  100% {
    -webkit-transform: translateX(0%) scaleX(1);
            transform: translateX(0%) scaleX(1);
    background-position: 0% 50%; } }

@keyframes leftRight {
  0% {
    -webkit-transform: translateX(0) scaleX(1);
            transform: translateX(0) scaleX(1);
    background-position: 0% 50%; }
  25% {
    -webkit-transform: translateX(200%) scaleX(0.2);
            transform: translateX(200%) scaleX(0.2); }
  50% {
    -webkit-transform: translateX(410%) scaleX(1);
            transform: translateX(410%) scaleX(1);
    background-position: 100% 50%; }
  75% {
    -webkit-transform: translateX(200%) scaleX(0.2);
            transform: translateX(200%) scaleX(0.2); }
  100% {
    -webkit-transform: translateX(0%) scaleX(1);
            transform: translateX(0%) scaleX(1);
    background-position: 0% 50%; } }

